body {
    background-color: #121212;
}

.flex {
    display: -ms-flexbox;
    display: flex;
}

.flex.center {
    -ms-flex-pack: center;
        justify-content: center;
}

.flex.space-around {
    -ms-flex-pack: distribute;
        justify-content: space-around;
}

.flex.space-between {
    -ms-flex-pack: justify;
        justify-content: space-between;
}

@media only screen and (max-width: 600px) {
    .flex.space-between {
        -ms-flex-pack: distribute;
            justify-content: space-around;
    }
}

.header {
    background-color: #212121;
    -ms-flex-direction: row;
        flex-direction: row;
    -ms-flex-align: center;
        align-items: center;
}

.header__link {
    font-size: 22px;
    color: #1db954;
}

.header__link, .create-players-btn, .create-players-btn__logout, .game__new-game-btn {
    text-transform: uppercase;
}

.header__link:hover, .header__link:focus, .header__link.active {
    font-size: 22px;
    color: #ffffff;
    text-decoration: underline;
}

.header__link:hover {
    color: #b3b3b3;
}

.create-players-form {
    padding: 0 20%;
}

.create-player-form__label {
    font-size: 20px;
    color: #1db954;
    margin: 12px 0;
}

.create-player-form__input, .create-player-form__input:disabled {
    background-color: #ffffff;
    border-color: #535353;
    color: #212121;
    font-size: 18px;
}

.create-player-form__checkbox {
    display: inline-block;
    color: #b3b3b3;
    font-size: 20px;
    line-height: 22px;
    padding: 6px 16px;
    margin: 16px 0;
}

.create-player-form__telegram {
    -ms-flex-align: center;
        align-items: center;
    margin-bottom: 16px;
}

.create-player-form__telegram-icon {
    margin-right: 6px;
}

.create-player-form__actions {
    -ms-flex-wrap: wrap;
        flex-wrap: wrap;
}

.create-players-btn, .game__new-game-btn {
    background-color: #1db954;
    color: white;
    border: none;
    display: block;
    padding: 10px 40px;
    margin: 6px 0;
    font-size: 20px;
}

.create-players-btn:disabled, .game__new-game-btn:disabled {
    background-color: #535353;
    border: none;
    display: block;
    padding: 10px 40px;
    font-size: 20px;
    color: #147d37;
}

.game__new-game-btn {
    margin: 18px auto;
}

.create-players-btn__logout {
    background-color: #212121;
    color: #535353;
    font-size: 20px;
    border: none;
}

.player-names {
    margin: 16px 0;
    color: white;
}

.player-name {
    padding: 8px 0;
    font-size: 20px;
}

.player-name.active {
    color: white;
    background-color: #1db954;
}

.game__recent-table {
    padding: 0;
}

.table-responsive {
    margin: 16px 0;
}

.history-table {
    background-color: white;
}



